.wrapper {
  position: relative;
  margin: 0 auto;
  width: 1280px;
  min-height: 100vh;

  // padding: 0 50px;

  @media (max-width: 1280px) {
    width: auto;
  }
  @media (max-width: 800px) {
    padding: 0 20px;
  }
}
.wrapper-grid {
  display: grid;
  grid-template-columns: auto 200px;
  width: 100%;
  height: 100%;

  @media (max-width: 950px) {
    display: none;
  }
}

.wrapper-grid__item {
  min-width: 100%;
  min-height: 100%;

  &:last-child {
    @media (max-width: 800px) {
      display: none;
    }
  }
}

