.sidebar__list-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.sidebar__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  padding: 10px 0;

  border-left: #000 1px solid;

  font-size: 14px;

  li {
    padding: 3px 0;

    @media (max-width: 950px) {
      display: block;
      width: 100%;
    }
  }

  @media (max-width: 950px) {
    padding: 0 5px;
  }
}

.social__list {
  display: flex;
  margin: 5px 5px 20px 5px;
}

.social__link {
  padding: 0 8px;

  img {
    transition: all 0.2s ease;
    &:hover {
      transform: scale(1.1);
      transition: all 0.2s ease;
    }
  }
}

.social__tel {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 20px;
}

.social__tel-img {
  width: 30px;
  transform: rotate(15deg);
}

.social__tel-link {
  color: #000;
  font-size: 14px;
}

//Swiper
.swiper {
  // position: absolute;
  // top: 0;
  // left: 0;
  width: 100%;
  height: 100vh;
}
.swiper-wrapper {
  height: 100vh;
}
.swiper-slide {
  height: 100vh;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 5px;
  padding: 20px 0;

  margin-top: 30px;

  @media (max-width: 650px) {
    display: block;
    margin: 15px;
    gap: 0;
    grid-template-columns: 0;

    margin-top: 70px;

    height: auto;
  }
}

.ant-float-btn-group {
  position: relative !important;
}

.wrapper-widget_switch {
  @media (max-width: 950px) {
    display: none;
  }
}
