.portfolio-content {
  display: flex;

  align-items: center;

  gap: 10px;

  padding-top: 40px;
  padding-bottom: 50px;

  height: calc(100% - 10px);

  word-wrap: break-word;

  overflow: hidden;

  @media (max-width: 700px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: calc(50% - 15px) calc(50% - 15px);

    padding-top: 60px;
    padding-bottom: 70px;

    img {
      width: auto;
      height: 100%;
    }
  }

  @media (max-width: 520px) {
    padding-top: 80px;
  }
}

// .swiper-slide:first-child .portfolio-content {
//   opacity: 0;
// }

.portfolio-content__item {
  display: flex;
  justify-content: center;

  height: 100%;
  padding: 20px 0;

  word-wrap: break-word;

  &:hover .portfolio-content-back {
    background-color: rgba(141, 99, 76, 0);
    transition: background-color 1s ease;
  }

  img {
    box-shadow: 0px 7px 7px 1px rgba(0, 0, 0, 0.5);
  }

  @media (max-width: 700px) {
    padding: 0;
  }

  &:last-child {
    @media (max-width: 700px) {
      grid-column: span 2;
    }
  }
}

