.portfolio-content__item-content {
  min-height: 100%;
  width: 100%;

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;

    @media (max-width: 1020px) {
      width: 100%;
    }
  }
}

.portfolio-content__item-button {
  align-self: flex-start;
  float: right;

  display: inline-flex;
  justify-content: space-between;
  align-items: end;

  font-weight: 500;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #323232;

  padding: 5px;
  margin: 14px 0 5px 0;

  width: 155px;
  transition: all 0.5s ease;

  cursor: pointer;

  &:hover {
    width: 170px;
    transition: all 0.5s ease;
    letter-spacing: 4px;
  }
}

.portfolio-content__item-button-icon {
  width: 18.4px;
  height: 18.4px;

  transform: rotate(-99grad);
}

.portfolio-content__item-title {
  position: absolute;
  bottom: 50px;
  right: 60px;

  color: #000;

  font-size: 22px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;

  display: inline-block;

  line-height: 0.8;

  span {
    font-size: 14px;
    margin-left: 10px;
    font-weight: normal;
  }

  @media (max-width: 520px) {
    bottom: 40px;
    right: 10px;

    text-align: right;
    width: 140px;

    span {
      display: none;
    }
  }
}

.portfolio-content__item-info {
  display: block;
  margin-bottom: 5px;
  font-size: 14px;

  span {
    display: block;

    font-weight: bold;
  }
}
